/**
 * @class
 */
class DeltaClasses {
  /**
   * @constructor
   * @param {string} prefix
   * @param {boolean} [prependPrefix=true]
   */
  constructor(prefix, prependPrefix = true) {
    /**
     * @type {string}
     * @private
     */
    this._prefix = prefix

    /**
     * @type {boolean}
     * @private
     */
    this._prependPrefix = prependPrefix

    /**
     * @type {string[]}
     * @private
     */
    this._classes = []
  }

  /**
   * @param {string} name
   * @param {boolean} [custom=false]
   * @return {DeltaClasses}
   */
  add(name, custom = false) {
    let cn = this._prefix
    if (custom) {
      cn += '-custom'
    }
    cn += `__${name}`

    this._classes.push(cn)

    return this
  }

  /**
   * @return {string}
   */
  export() {
    let classes = this._classes
    if (this._prependPrefix) {
      classes.unshift(this._prefix)
    }

    return classes.join(' ')
  }
}

export { DeltaClasses }
