import { component, html, useState } from "haunted";
import '@delta/progressbar-webcomp'
import { trust } from '@trust/trust-web-sdk-yapo';

const settings = {
  apiUrl: 'https://profile-api.trust.schibsted.io',
  userId: 'sdrn:yapo:user:fd2dcbee-000a-49f0-85d3-3510ef1304a3',
  locale: 'es-ES',
  debug: true,
  onError: function(err) {
    // your error handling code
    console.log('error', err)
  }
}

trust.profile.run([
  document.querySelector('[data-trust-communication-widget]')
], settings);


/**
 *
 * @param percentage
 * @returns {*}
 */
const validatePercentage = percentage => {
  if (isNaN(percentage) || Math.sign(percentage) === -1 || !percentage) return 0;
  if (percentage > 100) return 100;
  return Number(percentage);
}

/**
 * @return {TemplateResult}
 */
function MyApp(props) {

  const {holas} = props;
  const numeroUno = props['numero-uno'];
  const [hola, setHola] = useState('');
  const [nuevoNumero, setNuevoNumero] = useState(validatePercentage(numeroUno));

  const change = () => {
    setNuevoNumero(nuevoNumero + 10)
  };

  return html`
    <h2>User Page</h2>
    <h3>${holas}${hola}</h3>
    <p>${numeroUno}</p>
    <p>${nuevoNumero}</p>
    <button type="button" @click=${() => setHola(hola + 'Felipe')}>say hello</button>
    <button type="button" @click=${() => change()}>say hello</button>
  `;
}

MyApp.observedAttributes = ['holas','numero-uno'];

customElements.define("my-app", component(MyApp, HTMLElement, {useShadowDOM: false}));
