import { DeltaWebComponent } from '@delta/webcomponent'
import {
  bar,
  indicator,
  validatePercentage,
  CLASS_CONTAINER,
  CLASS_PROGRESS_BAR
} from '../../fsm'
import {
  INDICATOR_TYPE,
  INDICATOR_VERTICAL_POSITION,
  INDICATOR_HORIZONTAL_POSITION
} from '../../modifiers'

import style from './progressbar.scss'

/**
 * @class
 * @extends DeltaWebComponent
 * @delta-import DeltaWebComponent from '@delta/webcomponent'
 */
class DeltaProgressBar extends DeltaWebComponent {
  /**
   * @type {Array<string>}
   */
  static get props() {
    return [
      'indicator-vertical-position',
      'indicator-horizontal-position',
      'indicator-type',
      'percentage',
      'animated'
    ]
  }

  /**
   * @type {Object<string,string>}
   */
  static get defaults() {
    return {
      'indicator-vertical-position': INDICATOR_VERTICAL_POSITION.top,
      'indicator-horizontal-position': INDICATOR_HORIZONTAL_POSITION.left,
      'indicator-type': INDICATOR_TYPE.percentage
    }
  }

  /**
   * @param {Object<string,string>} current
   * @return {void}
   */
  render(current) {
    this.shadowRoot.innerHTML = this.getTemplate()

    let {
      'indicator-vertical-position': indicatorVerticalPosition,
      'indicator-horizontal-position': indicatorHorizontalPosition,
      'indicator-type': indicatorType,
      percentage
    } = current

    percentage = validatePercentage(percentage)

    const animated = this.attributeIsSetOrHasValue('animated', 'true')

    const element = this.shadowRoot.querySelector('div.delta-progressBar')

    const indicatorSpan = document.createElement('span')
    const indicatorClasses = indicator({ indicatorHorizontalPosition })

    if (indicatorType === INDICATOR_TYPE.percentage) {
      indicatorSpan.innerHTML = `<strong>${percentage}</strong>%`
    } else {
      indicatorSpan.innerHTML = `<strong>${percentage}</strong>/100`
    }
    indicatorSpan.setAttribute('class', indicatorClasses.export())

    if (indicatorVerticalPosition === INDICATOR_VERTICAL_POSITION.top) {
      element.prepend(indicatorSpan)
    } else {
      element.append(indicatorSpan)
    }

    /** @type {HTMLElement} */
    const barSpan = this.shadowRoot.querySelector(
      '.delta-progressBar__wrapper span'
    )
    const barClasses = bar({ animated })
    const attributes = {
      style: `width: ${percentage}%`,
      class: barClasses.export()
    }

    this.updateAttributes(attributes, barSpan)
  }

  /**
   * @return {string}
   */
  getTemplate() {
    let template = `<style>${style}</style>`
    template += `
     <div class="${CLASS_PROGRESS_BAR}">
        <div class="${CLASS_CONTAINER}">
          <span />
        </div>
      </div>
    `

    return template
  }
}

export { DeltaProgressBar }
