/** @type {string[]} */
const NON_BUBBLING_EVENTS = ['change', 'submit', 'reset']

/**
 * According to the DOM specs, not all events bubble, which may cause
 * listeners triggered from within the web component to not be triggered
 *
 * Example:
 *
 *  <delta-web-component>
 *      <textarea></textarea>
 *  </delta-web-component>
 *
 * Changes on the textarea should be captured on the web component by adding
 * an 'onChange' listener
 *
 * @param {HTMLElement} element
 * @param {string[]} events
 * @param {bool} concat
 * @return {void}
 */
const propagateEvents = function(element, events = [], concat = true) {
  if (concat) {
    events = events.concat(NON_BUBBLING_EVENTS)
  }
  events.forEach(name => {
    element.addEventListener(name, event => {
      const bubble = new event.constructor(event.type, event)
      this.dispatchEvent(bubble)
    })
  })
}

export { propagateEvents }
