/* global HTMLElement MutationObserver */

import throttle from 'lodash.throttle'

/**
 * @class
 * @extends HTMLElement
 */
class DeltaWebComponent extends HTMLElement {
  /**
   * @type {Array<string>}
   */
  static get props() {
    return []
  }

  /**
   * @type {Object<string,string>}
   */
  static get defaults() {
    return {}
  }

  /**
   * HTML attributes to be skipped during automatic update operations
   * such as style. These are generally added by the browser as a result
   * of user actions. Ex: resizing a textarea
   *
   * @type {Array<string>}
   */
  static get skipAutoUpdate() {
    return ['style']
  }

  /**
   * @constructor
   */
  constructor() {
    super()

    /**
     * @type {boolean}
     * @private
     */
    this._useShadowRoot = true

    /**
     * @type {Object<string,boolean>}
     * @private
     */
    this._watchedSlots = {}

    /**
     * @type {MutationObserver}
     * @private
     */
    this._observer = null

    /**
     * @type {function}
     * @private
     */
    this._throttledRender = throttle(function() {
      const attributes = (this.constructor.props || []).reduce((obj, name) => {
        let value = null
        if (this.hasAttribute(name)) {
          value = this.getAttribute(name)
        } else {
          if (this.constructor.defaults[name]) {
            value = this.constructor.defaults[name]
          }
        }
        obj[name] = value

        return obj
      }, {})

      this.render(attributes)
    }, 100)
  }

  /**
   * @return {void}
   */
  connectedCallback() {
    const template = this.getTemplate()
    if (this._useShadowRoot) {
      this.attachShadow({ mode: 'open' })
    }

    if (template) {
      ;(this.shadowRoot || this).innerHTML = this.getTemplate()
    }

    const slot = this.getSlot()
    if (slot) {
      this.setAttribute('slot', slot)
    }

    this._observer = new MutationObserver(() => {
      this._throttledRender()
    })
    this._observer.observe(this, {
      attributes: true,
      childList: true,
      subtree: true
    })

    this._throttledRender()
  }

  /**
   * @return {Object<string, string>}
   */
  getNonPropsAttributes() {
    return [...this.attributes]
      .map(item => item.name)
      .filter(
        name =>
          this.constructor.props.indexOf(name) === -1 &&
          this.constructor.skipAutoUpdate.indexOf(name) === -1
      )
      .reduce((obj, name) => {
        obj[name] = this.getAttribute(name)
        return obj
      }, {})
  }

  /**
   * @param {string} attribute
   * @param {string|Array<string>} value
   * @return {boolean}
   */
  attributeIsSetOrHasValue(attribute, value) {
    if (typeof value === 'string') {
      value = [value]
    }
    return (
      this.hasAttribute(attribute) &&
      (this.getAttribute(attribute) === '' ||
        value.indexOf(this.getAttribute(attribute)) !== -1)
    )
  }

  /**
   * @param {Object<string,string>} attributes
   * @param {HTMLElement} element
   * @return {void}
   */
  updateAttributes(attributes, element) {
    const currentAttributes = [...element.attributes].map(item => item.name)

    currentAttributes.forEach(key => {
      if (this.constructor.skipAutoUpdate.indexOf(key) !== -1) {
        return
      }

      if (!attributes[key]) {
        element.removeAttribute(key)
      } else {
        if (element.getAttribute(key) !== attributes[key]) {
          element.setAttribute(key, attributes[key])
        }

        delete attributes[key]
      }
    })

    Object.keys(attributes).forEach(key => {
      element.setAttribute(key, attributes[key])
    })
  }

  /**
   * @param {string} slot
   * @return {Boolean}
   */
  isSlotLoaded(slot = null) {
    slot = slot || 'unnamed'
    return this._watchedSlots[slot] === true
  }

  /**
   * @param {Function} fn
   * @param {string} slot
   * @return {DeltaWebComponent}
   */
  watchSlot(fn, slot = null) {
    const slotRef = this.shadowRoot.querySelector(
      slot ? `slot[name=${slot}]` : 'slot'
    )

    slot = slot || 'unnamed'

    if (!slotRef) {
      console.log(`Slot not found: "${slot}"`)
    }

    slotRef.addEventListener('slotchange', e => {
      fn.call(this, e)
      this._watchedSlots[slot] = true
    })

    return this
  }

  /**
   * @return {string}
   */
  getTemplate() {
    return ''
  }

  /**
   * @return {string}
   */
  getSlot() {
    return ''
  }

  /**
   * @param {Object<string,string>} current      current values for props
   * @return {void}
   */
  render(current) {}
}

export { DeltaWebComponent }
