/**
 * Proxies properties changes from a HTMLElement to a Web Component and vice-versa
 *
 * This function is used to keep in sync attributes defined on the web
 * component with its internals.
 *
 * Example case: keeping the outer `value` attribute in sync with the
 * internal `value` on the textarea
 *
 *  <delta-web-component value="">
 *    #shadowRoot
 *      <!-- other elements -->
 *      <textarea value=""></textarea>
 *  </delta-web-component>
 *
 *  This function should be used for values that rely on setters and getters
 *  to be passed, not (set/get)Attribute as those are picked up by the
 *  MutationObserver
 *
 *  This function assumes `this` as the web component, so always use bind(),
 *  call() or apply()
 *
 * @param {HTMLElement} element       HTML element to keep in sync with the web component
 * @param {string|string[]} fields    List of attributes to the synced
 * @return {void}
 */
const proxy = function(element, fields) {
  if (typeof fields === 'string') {
    fields = [fields]
  }
  fields.forEach(field => {
    Object.defineProperty(this, field, {
      get() {
        return element[field]
      },
      set(value) {
        element[field] = value
      }
    })
  })
}

export { proxy }
