import { DeltaClasses } from '@delta/shared'

const DELTA_PREFIX = 'delta-progressBar'

const CLASS_PROGRESS_BAR = `${DELTA_PREFIX}`
const CLASS_CONTAINER = `${DELTA_PREFIX}__wrapper`

/**
 * @param {Object<string,any>} params
 * @return {DeltaClasses}
 */
const bar = ({ animated }) => {
  const classes = new DeltaClasses(DELTA_PREFIX, false)
  classes.add('progress')

  if (animated) {
    classes.add('animated')
  }

  return classes
}

/**
 * @param {Object<string,any>} params
 * @return {DeltaClasses}
 */
const indicator = ({ indicatorHorizontalPosition: position }) => {
  const classes = new DeltaClasses(DELTA_PREFIX, false)
  classes.add('indicator')
  classes.add(`indicator-${position}`)

  return classes
}

/**
 * @param {number} percentage
 * @returns {number}
 */
const validatePercentage = percentage => {
  if (isNaN(percentage) || Math.sign(percentage) === -1 || !percentage) return 0
  if (percentage > 100) return 100
  return Number(percentage)
}

export {
  bar,
  indicator,
  validatePercentage,
  CLASS_PROGRESS_BAR,
  CLASS_CONTAINER
}
