/**
 * @param {Object<string,any>} props
 * @return {Object<string,any>}
 */
const rel = props => {
  if (props.target && props.target === '_blank') {
    let values = []

    if (props.rel) {
      values = props.rel.split(' ')
    }

    if (values.indexOf('noopener') === -1) {
      values.push('noopener')
    }
    if (values.indexOf('noreferrer') === -1) {
      values.push('noreferrer')
    }

    props.rel = values.join(' ')
  }

  return props
}

export { rel }
